/* -----------------------------------------------------------------------------
HAMBURGER ICONS COMPONENT
----------------------------------------------------------------------------- */
/**
* Toggle Switch Globals
*
* All switches should take on the class `c-hamburger` as well as their
* variant that will give them unique properties. This class is an overview
* class that acts as a reset for all versions of the icon.
*/
.c-hamburger__container {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 40px;
  height: 40px;
  position: absolute;
  right: 0;
  top: 0;
}

.c-hamburger__container::after {
  content: '';
  width: 10px;
  height: 10px;
  opacity: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate3d(-50%, -50%, 0) scale(1);
  transition: all .3s ease;
}

@media (min-width: 1280px) {
  .c-hamburger__container {
    display: none;
  }
}

body.is-active .c-hamburger__container::after {
  transform: translate3d(-50%, -50%, 0) scale(7);
  opacity: 1;
}

.c-hamburger {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 auto;
  display: block;
  position: relative;
  overflow: hidden;
  padding: 0;
  width: 24px;
  height: 24px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: 0;
  cursor: pointer;
  z-index: 1;
  transition: background 0.3s;
}

.c-hamburger span {
  background: #fff;
  display: block;
  position: absolute;
  top: 11px;
  left: 0;
  right: 0;
  height: 2px;
}

.c-hamburger:focus {
  outline: none;
}

.c-hamburger span::after,
.c-hamburger span::before {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  content: '';
}

.c-hamburger span::before {
  top: -9px;
}

.c-hamburger span::after {
  bottom: -9px;
}

/**
* Style 1
*
* Rotating hamburger icon (rot), that simply rotates 90 degrees when activated.
* Nothing too fancy, simple transition.
*/
.c-hamburger--rot {
  background-color: #28aadc;
}

.c-hamburger--rot span {
  transition: transform 0.3s;
}

/* active state, i.e. menu open */
.c-hamburger--rot.is_active {
  background-color: #166888;
}

.c-hamburger--rot.is_active span {
  transform: rotate(90deg);
}

/**
* Style 2
*
* Hamburger to 'x' (htx). Takes on a hamburger shape, bars slide
* down to center and transform into an 'x'.
*/
.c-hamburger--htx {
  background-color: transparent;
}

.c-hamburger--htx span::after,
.c-hamburger--htx span::before {
  transition-duration: 0.3s, 0.3s;
}

.c-hamburger--htx span::before {
  transition-property: top, transform;
}

.c-hamburger--htx span::after {
  transition-property: bottom, transform;
}

body.is-active {
  /* active state, i.e. menu open */
}

body.is-active .c-hamburger--htx {
  background-color: transparent;
}

body.is-active .c-hamburger--htx span {
  background: none;
}

body.is-active .c-hamburger--htx span::before {
  top: 0;
  transform: rotate(45deg);
}

body.is-active .c-hamburger--htx span::after {
  bottom: 0;
  transform: rotate(-45deg);
}

body.is-active .c-hamburger--htx span::after,
body.is-active .c-hamburger--htx span::before {
  transition-delay: 0s, 0.3s;
}

