    lost-column: 10/12 flex;
  }
  .l-lost-column--eleven-twelfths {
    lost-column: 11/12;
  }
  .l-lost-column--eleven-twelfths--flex {
    lost-column: 11/12 flex;
  }
  .l-lost-column--twelve-twelfths {
    lost-column: 12/12;
  }
  .l-lost-column--twelve-twelfths--flex {
    lost-column: 12/12 flex;
  }
}

.l-lost-three-col {
  lost-column: 1/1 flex;
}

@media (min-width: 768px) {
  .l-lost-three-col {
    lost-column: 1/2 flex;
  }
}

@media (min-width: 1024px) {
  .l-lost-three-col {
    lost-column: 1/3 flex;
  }
}
